.about {
   background-color: var(--white-color);

   &__descr {
      margin: 0;
      max-width: 892px;
      font-weight: 400;
      font-size: 30px;
      line-height: 160%;
      color: var(--dark-color);

      &-highlight {
         font-weight: 600;
      }
   }

   &__list {
      display: grid;
      grid-template-columns: repeat(12, 1fr);
      gap: 40px;
   }

   &__item {
      grid-column: 3 span;
   }


   &__digit {
      font-weight: 600;
      font-size: 70px;
      line-height: 160%;
      color: var(--accent-color);
   }

   &__text {
      margin: 0;
      font-weight: 400;
      font-size: 18px;
      line-height: 160%;
      color: var(--text-color);
   }
}