.benefits {
   background-color: #F9F9F9;

   &__list {
      display: grid;
      grid-template-columns: repeat(12, 1fr);
      gap: 40px;
   }

   &__item {
      grid-column: 4 span;
   }

   &__subtitle {
      display: inline-flex;
      align-items: center;
      font-weight: 600;
      font-size: 24px;
      line-height: 160%;
      color: var(--dark-color);
   }

   &__descr {
      font-weight: 400;
      font-size: 18px;
      line-height: 160%;
      color: var(--text-color);
   }
}