.tooltip {
   margin-left: 10px;

   &__btn {
      @include flex-all-center;
      width: 24px;
      height: 24px;
      border-radius: 100%;
      background: rgba(20, 16, 36, 0.08);

      svg {
         width: 4px;
         height: 14px;
      }

      &:focus {
         +.tooltip__txt {
            opacity: 1;
            visibility: visible;
         }
      }

      @include hover {
         +.tooltip__txt {
            opacity: 1;
            visibility: visible;
         }
      }
   }



   &__txt {
      box-shadow: 0px 2px 8px rgba(20, 16, 36, 0.1);
      border-radius: 5px;
      font-weight: 400;
      font-size: 16px;
      line-height: 160%;
      color: var(--dark-color);
      background-color: var(--light-color);
      padding: 12px 20px;
      opacity: 0;
      visibility: hidden;
      transition: 0.3s ease-in-out, visibility 0.3s ease-in-out;
   }
}